.Mainsection{
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 250px;
    color: white;
    display: inline-block;
    /* width: 50%; */
    
}
.maintext{
    padding-right: 150px;
}
.inputtag{
    border-radius: 5px;
    outline: none;
    color: black;
   
    font-weight: 400;
}

.header{
    width: 100%;
    display: flex;
    align-items: center;
    margin-bottom: 12px;
}
.header-logo{
    height: 40px;
    max-width: 140px;
    margin-right: 24px;
}
.header-rigth{
    display: flex;
    flex: 1;
    justify-content: space-between;
    align-items: center;
    height: 72px;
}
.text-city{
    margin-right: 80px;
}
.header-location-search-container{
    height: 55px;
    display: flex;
    align-items: center;
    /* margin-left: 2px; */
    border-radius: 8px;
    box-shadow: rgb(28 28 28/8%)0px 2px 8px;
    border: 1px solid rgb(232,232,232);
    width: 70%;
    background-color: rgb(255,255,255);
    justify-content: center;
    align-items: center;
    display: flex;
    margin-top: 402px;
    margin-left: 183px;
}
.location-wrapper{
    display: flex;
    flex: 1;
    justify-content: space-between;
    padding: 0px 10px;  
}
.search-icon{
    font-size: 20px;
    color: #828282;
    margin: 0px 12px;
}

.location-icon-name{
    display: flex;
    color:#828282;
}
.location-icon{
    color:red;
    font-size: 20px;
    margin-right: 20px;
}
.location-search-seprator{
    height: 20px;
    border: 0.5px solid #828282;
    display: flex;
}
.header-search-bar{
    display: flex;
    flex: 2;
}
.search-icon{
    font-size: 20px;
    color: #828282;
    margin: 0px 12px;
}
.search-input{
    border: none;
    outline: none;
    width: 60%;
    font-size: 14px;
}
.profile-wrapper{
    display: flex;
    justify-content: space-between;
    width: 15%;
    align-items: center;
}




