.row{
    /* padding-top:5rem; */
}
.discriptions{
    font-size:medium;
    font-weight: 400;
}
.product{
    overflow: hidden;
    width: 255px;
    text-align: center;
    background-color: #fff;
    border: 1px solid gray;
    box-shadow: 0 3px 24px rgb(0 0 0 /4%);
    padding: 25px;
    margin: 0 25px ;
    border-radius: 10px;
    -webkit-box-shadow: 7px 14px 10px -1px rgba(232,28,21,1);
-moz-box-shadow: 7px 14px 10px -1px rgba(232,28,21,1);
box-shadow: 7px 14px 10px -1px rgba(232,28,21,1);
}
.title,h6{
    margin-bottom: 10px;
    font-size: 20px;
    color: black;
    font-weight: 800;
}
.star i{
    margin-right: 5px;
    font-size: 12px;
    color: #e4e01c;
}
.fa-solid{
    color: red;
}
.display{
    display: flex;
    
}
.wrapper{
    width: 4.47392e7px;
    opacity: 1;
    transform: translate3d(0px, 0px, 0px);
    display: flex;
    align-items: center;
    justify-content: center;
}
@media screen  and (max-width: 669px){
    .title{
        width: 355px;
    }
}
@media screen and (max-width:350px){
    .title{
        width: 355px;
    }
} 
    
.d-flex{
    display: flex;
    align-items: center;
    justify-content: center;
}